<template>
  <div>
    <!-- 头部面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品列表</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片 -->
    <el-card shadow="always">
      <el-input placeholder="请输入内容" class="input-with-select">
        <el-button slot="append" icon="el-icon-search"></el-button>
      </el-input>
      <el-button type="primary">添加商品</el-button>

      <!-- 商品列表表格 -->
      <el-table :data="goodsItem" border>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column label="商品名称" prop="goods_name" width="600px"></el-table-column>
        <el-table-column label="商品价格(元)" prop="goods_price"></el-table-column>
        <el-table-column label="商品重量" prop="goods_weight"></el-table-column>
        <el-table-column label="创建时间" prop="upd_time"></el-table-column>
        <el-table-column label="操作">
            <template>
                 <el-button type="primary" icon="el-icon-edit" circle></el-button>
                   <el-button type="danger" icon="el-icon-delete" circle></el-button>
            </template>
        </el-table-column>
      </el-table>
       <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :page-sizes="[5,10,20,30]"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 5,
      },
      goodsItem: [],
      total: 0,
    }
  },
  methods: {
    async getGoodList() {
      const { data: res } = await this.$http.get('goods', {
        params: this.queryInfo,
      })
      console.log(res)
      if (res.meta.status != 200) return this.$message.error('商品列表请求失败')
      this.goodsItem = res.data.goods
      this.total = res.data.total
    },
    handleSizeChange(newPage){
       this.queryInfo.pagesize=newPage
       this.getGoodList()
    },
    handleCurrentChange(newPage){
        this.queryInfo.pagenum=newPage
        this.getGoodList()
    }
  },
  mounted() {
    this.getGoodList()
  },
}
</script>

<style>
.el-card {
  margin-top: 15px;
}
.el-input {
  width: 400px;
}
.el-button {
  margin-left: 15px;
}
.el-table{
    margin-top:15px;
}
.el-pagination{
    margin-top:15px;
}
</style>